*, :before, :after {
  box-sizing: border-box;
}

body {
  background-color: #292929;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.progressbar {
  --color: #00ba10;
  --percent: 0;
  --r: 30;
  --PI: 3.14;
  --lengthCircle: calc(2 * var(--PI) * var(--r));
  --opacityThumb: 0;
  max-width: 450px;
  font-size: 10px;
  display: block;
}

.progressbar circle {
  r: var(--r);
  stroke-width: 2px;
  fill: #0000;
}

.progressbar__track {
  stroke: #ededed;
}

.progressbar__thumb {
  stroke: var(--color);
  stroke-dasharray: calc(var(--lengthCircle) * var(--percent) / 100) var(--lengthCircle);
  stroke-linecap: round;
  transform-origin: center;
  transform-box: fill-box;
  opacity: var(--opacityThumb);
  transform: rotate(-90deg);
}

.progressbar text {
  fill: var(--color);
}

.progressbar__percent {
  font-size: 1.6em;
}
